/* Social hint dimensions */
.contact-form {
  display: block;
  width: 100%; }

.contact-form__input {
  display: block;
  width: 100%;
  background-color: transparent;
  border: 0;
  font-size: 18px;
  font-weight: 300;
  padding: 0.6rem 19px;
  box-sizing: border-box;
  border-left: 1px solid #1184FE;
  color: rgba(255, 255, 255, 0.8);
  margin: 0.8rem 0;
  outline: none;
  max-height: 260px;
  max-width: 560px; }

.contact-form__input:hover, .contact-form__input:focus {
  transition: all 0.1s;
  box-sizing: border-box;
  padding: 0.6rem 17px;
  border-left: 3px solid #1184FE;
  background: rgba(255, 255, 255, 0.05); }

.contact-form__input--error {
  display: none;
  font-size: 14px;
  color: #f30000;
  margin-bottom: 1rem; }

.contact-form__message {
  display: none;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.8); }
